import React, { Component } from 'react'
import {Nav,Navbar,Container,Row,Col} from 'react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHome, faAddressCard, faAddressBook } from "@fortawesome/free-solid-svg-icons";
import { SocialIcon } from 'react-social-icons';

export default class Topnavbar extends Component {
    constructor(props){
        super(props);
        this.state = {
            onHoverSocialIcon: -1
        }
        this.SetonHoverSocialIcon = this.SetonHoverSocialIcon.bind(this);
    }
    
    SetonHoverSocialIcon = (index) => {
        this.setState({
            ...this.state,
            onHoverSocialIcon: index
        })
        console.log(index);
    }

    render() {
        return (
                <Container>
                    <Row>
                        <Navbar fixed="top">
                            <Col>
                                <span className="float-right">
                                    <SocialIcon url="https://www.linkedin.com/in/rahul-bhosale-20881a107"
                                        onMouseEnter={(e) => this.SetonHoverSocialIcon(0)}
                                        onMouseLeave={(e) => this.SetonHoverSocialIcon(-1)}
                                        fgColor={this.state.onHoverSocialIcon === 0 ? "#cf8c11d9" : "#fff" }
                                        style={{ height: 40, width: 40 ,'border-radius': '28px', 'box-shadow': this.state.onHoverSocialIcon === 0 && '0 0 20px rgba(255,238,0, 0.6)', 'transition': 'all 500ms ease-out'}}
                                    />
                                    <SocialIcon url="https://www.github.com/rtbhosale"
                                        onMouseEnter={(e) => this.SetonHoverSocialIcon(1)}
                                        onMouseLeave={(e) => this.SetonHoverSocialIcon(-1)}
                                        fgColor={this.state.onHoverSocialIcon === 1 ? "#cf8c11d9" : "#fff" }
                                        style={{ height: 40, width: 40 ,'border-radius': '28px', 'box-shadow': this.state.onHoverSocialIcon === 1 && '0 0 20px rgba(255,238,0, 0.6)', 'transition': 'all 500ms ease-out'}}                                    />
                                    <SocialIcon url="https://www.facebook.com/rtbhosale"
                                        onMouseEnter={(e) => this.SetonHoverSocialIcon(2)}
                                        onMouseLeave={(e) => this.SetonHoverSocialIcon(-1)}
                                        fgColor={this.state.onHoverSocialIcon === 2 ? "#cf8c11d9" : "#fff" }
                                        style={{ height: 40, width: 40 ,'border-radius': '28px', 'box-shadow': this.state.onHoverSocialIcon === 2 && '0 0 20px rgba(255,238,0, 0.6)', 'transition': 'all 500ms ease-out'}}
                                    />
                                    <SocialIcon url="https://twitter.com/rtbhosale17"
                                        onMouseEnter={(e) => this.SetonHoverSocialIcon(3)}
                                        onMouseLeave={(e) => this.SetonHoverSocialIcon(-1)}
                                        fgColor={this.state.onHoverSocialIcon === 3 ? "#cf8c11d9" : "#fff" }
                                        style={{ height: 40, width: 40 ,'border-radius': '28px', 'box-shadow': this.state.onHoverSocialIcon === 3 && '0 0 20px rgba(255,238,0, 0.6)', 'transition': 'all 500ms ease-out'}}
                                    />                            
                                    <SocialIcon url="mailto://rtbhosale17@gmail.com"
                                        onMouseEnter={(e) => this.SetonHoverSocialIcon(4)}
                                        onMouseLeave={(e) => this.SetonHoverSocialIcon(-1)}
                                        fgColor={this.state.onHoverSocialIcon === 4 ? "#cf8c11d9" : "#fff" }
                                        style={{ height: 40, width: 40 ,'border-radius': '28px', 'box-shadow': this.state.onHoverSocialIcon === 4 && '0 0 20px rgba(255,238,0, 0.6)', 'transition': 'all 500ms ease-out'}}
                                    /> 
                                </span>
                            </Col>
                        </Navbar>
                    </Row>
                </Container>
        )
    }
}
